<template>
    <div class="setup-box">
        <div class="setup-nav-line">
            <div class="route">
                <router-link class="icon-route" to="/setup/setbase">
                    <IconTextButton module="DEV_CONFIG" :permission="1" :iconName="baseIcon" :text="$t('setbase.setbase')" iconSize="2.5rem" interval="-0.2rem" fontSize="1.375rem"></IconTextButton>
                </router-link>
            </div>
            <div class="vertical-line">
                <span class="line"></span>
            </div>
            <div class="route">
                <router-link class="icon-route" to="/setup/control">
                    <IconTextButton module="DEV_CONTROL" :permission="1" :iconName="controlIcon" :text="$t('setcontrol.setcontrol')" iconSize="2.5rem" interval="-0.2rem" fontSize="1.375rem"></IconTextButton>
                </router-link>
            </div>
            <div class="vertical-line">
                <span class="line"></span>
            </div>
            <div class="route">
                <router-link class="icon-route" to="/setup/network">
                    <IconTextButton module="DEV_BUS" :permission="1" :iconName="networkIcon" :text="$t('setnet.setnet')" iconSize="2.5rem" interval="-0.2rem" fontSize="1.375rem"></IconTextButton>
                </router-link>
            </div>
            <div class="vertical-line">
                <span class="line"></span>
            </div>
            <div class="route">
                <router-link class="icon-route" to="/setup/setuser">
                    <IconTextButton module="AUTH_GROUP" :permission="1" :iconName="userIcon" :text="$t('setuser.usermange')" iconSize="2.5rem" interval="-0.2rem" fontSize="1.375rem"></IconTextButton>
                </router-link>
            </div>
        </div>
        <div class="setup-content">
            <router-view></router-view>
        </div>
    </div>
</template>
<script>
import IconTextButton from '../components/controls/IconTextButton.vue'

export default {

    components:{
        IconTextButton
    },
    computed:{
        baseIcon(){
            return (this.$route.path.indexOf('setbase')!=-1) ? "ActiveCube" : "StaticCube"
        },
        controlIcon(){
            return (this.$route.path.indexOf('control')!=-1) ? "ActiveCmtr" : "StaticCptr"
        },
        networkIcon(){
            return (this.$route.path.indexOf('network')!=-1) ? "ActiveIntrfc" : "StaticIntrfc"
        },
        userIcon(){
            return (this.$route.path.indexOf('setuser')!=-1) ? "AliveUsers" : "StaticUsers"
        },
    },
}
</script>

<style scoped>
a{
    text-decoration: none;
    color:var(--C3B);
}
*{
    font-size:var(--font1_625) !important;
}
.setup-box{
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    
}
.router-link-inactive{
   pointer-events: none;

}
.setup-nav-line{
    position: relative;
    width: 100%;
    height: 7.3438vh;
    display: flex;
    background-color:var(--Cee);
}
.setup-content{
    position: relative;
    flex: 1;
}
.route{
    flex: 6;
}
.icon-route{
    position: relative;
    top: 11%;

}
.route div{
    font-size:var(--font1_625);
    color: var(--C3B) !important;

}
.router-link-active{
    font-weight: 600;
    color: var(--C00);
}
.vertical-line{
    flex: 1;
    height: 4rem;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}
.line{
    display: block;
    height: 2rem;
    width: 2px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background-color: var(--Cc6);
}
</style>